import {FilterValueType, todoListType} from "../AppWithRedux";
import {v1} from "uuid";

const initialState: Array<todoListType> = []

export const todolistsReducer = function(state: Array<todoListType> = initialState, action: todolistsReducerType) {
    switch (action.type) {
        case "REMOVE-TODOLIST":
            return state.filter(el => el.id !== action.payload.todolistId)
        case "ADD-TODOLIST":
            const todoList: todoListType = {
                id: action.payload.todolistId,
                title: action.payload.title,
                filter: "all",
                selectHandler: false
            }
            return [todoList, ...state]
        case "CHANGE-TODOLIST-TITLE": {
            return state.map(el => el.id === action.payload.todolistId ? {...el, title: action.payload.title} : el)
        }
        case "CHANGE-TODOLIST-FILTER": {
            return state.map(el => el.id === action.payload.todolistId ? {...el, filter: action.payload.filter} : el)
        }
        case "SELECT-ALL-ITEMS": {
            let todolist = state.find(todolist => todolist.id === action.payload.todolistId)
            if(todolist) {
                todolist.selectHandler = action.payload.isChecked
                if(todolist.selectHandler) {
                console.log(action.payload)
                console.log(action.payload.tasksObj)
                console.log(action.payload.todolistId)
                console.log(action.payload.tasksObj[action.payload.todolistId])
                   action.payload.tasksObj[action.payload.todolistId].map((el:any) => {
                       el.isDone = true
                    })
                } else if(todolist.selectHandler === false) {
                    action.payload.tasksObj[action.payload.todolistId].map((el:any) => {
                        el.isDone = false
                    })
                }
            }
            return [...state]
        }
        default: return state
    }
}
type todolistsReducerType = removeTodolistType | addTodolistType | changeTodolistTitleType | filterTasksType | selectAllItemsType
export type removeTodolistType = ReturnType<typeof removeTodolistAC>
export type addTodolistType = ReturnType<typeof addTodolistAC>
type changeTodolistTitleType = ReturnType<typeof changeTodolistTitleAC>
type filterTasksType = ReturnType<typeof filterTasksAC>
export type selectAllItemsType = ReturnType<typeof selectAllItemsAC>

export const removeTodolistAC = (todolistId: string) => {
    return {
        type: 'REMOVE-TODOLIST',
        payload: {
            todolistId
        }
    } as const
}
export const addTodolistAC = (title: string) => {
    return {
        type: 'ADD-TODOLIST',
        payload: {
            title,
            todolistId: v1()
        }
    } as const
}
export const changeTodolistTitleAC = (title: string, todolistId: string) => {
    return {
        type: 'CHANGE-TODOLIST-TITLE',
        payload: {
            title,
            todolistId
        }
    } as const
}
export const filterTasksAC = (filter: FilterValueType, todolistId: string) => {
    return {
        type: 'CHANGE-TODOLIST-FILTER',
        payload: {
            filter,
            todolistId
        }
    } as const
}
export const selectAllItemsAC = (isChecked: boolean, todolistId: string, tasksObj: any) => {
    return {
        type: 'SELECT-ALL-ITEMS',
        payload: {
            isChecked,
            todolistId,
            tasksObj
        }
    } as const
}

