import React, {useCallback} from 'react';
import './App.css';
import {TodoList} from "./TodoList";
import {v1} from "uuid";
import Header from "./Header";
import {AddItemForm} from "./AddItemForm";
import {Container, Grid, Paper} from "@mui/material";

import {
    addTodolistAC,
    changeTodolistTitleAC,
    filterTasksAC,
    removeTodolistAC, selectAllItemsAC
} from "./state/todolists-reducer";
import {addTasktAC, changeTaskStatusAC, changeTaskTitleAC, removeTasktAC} from "./state/tasks-reducer";
import {useDispatch, useSelector} from "react-redux";
import {AppRootStateType} from "./state/store";


export type FilterValueType = "all" | "completed" | "active"

export type todoListType = {
    id: string
    title: string
    filter: FilterValueType
    selectHandler: boolean
}

export type taskType = {
    id: string
    title: string
    isDone: boolean
}

export type tasksStateType = {
    [key: string]: taskType[]
}

function AppWithRedux() {

    let todoListId1 = v1()
    let todoListId2 = v1()

    let todolists = useSelector<AppRootStateType, todoListType[]>(state => state.todolists)
    let tasks = useSelector<AppRootStateType, tasksStateType>(state => state.tasks)

    const dispatch = useDispatch()

    const removeTodoList = useCallback((todoListId: string) => {
        dispatch(removeTodolistAC(todoListId))
    }, [dispatch])

    const addTodolist = useCallback((title: string) => {
        dispatch(addTodolistAC(title))
    }, [dispatch])

    const filterTasks = useCallback((filter: FilterValueType, todoListId: string) => {
        dispatch(filterTasksAC(filter, todoListId))

    }, [dispatch])

    const changeTodolistTitle = useCallback((newValue: string, todolistId: string) => {
        dispatch(changeTodolistTitleAC(newValue, todolistId))
    }, [dispatch])


    const addTask = useCallback((title: string, todoListId: string) => {
        dispatch(addTasktAC(todoListId, title))
    }, [dispatch])

    const removeTask = useCallback((id: string, todoListId: string) => {

        dispatch(removeTasktAC(id, todoListId));
    }, [dispatch])

    const changeTaskStatus = useCallback((taskId: string, isDone: boolean, todoListId: string) => {
        dispatch(changeTaskStatusAC(taskId, isDone, todoListId))
    }, [dispatch])


    const onCnangeListItemHandler = useCallback((newValue: string, id: string, todolistId: string) => {

        dispatch(changeTaskTitleAC(id, newValue, todolistId));

    }, [])

    const selectAllItems = useCallback((todolistID: string, isChecked: boolean, tasks) => {
        debugger;
        dispatch(selectAllItemsAC(isChecked, todolistID, tasks))
    }, [dispatch, selectAllItemsAC, tasks])


    return (

        <div className="App">
            <Header/>
            <Container fixed>
                <Grid container style={{padding: "20px"}}>
                    <AddItemForm label={'New todolist'} addItem={addTodolist}/>
                </Grid>
                <Grid container spacing={3}>
                    {
                        todolists.map((el: any) => {
                            let tasksForTodoList = tasks[el.id];

                            return <Grid item>
                                <Paper style={{padding: "10px"}}>
                                    <TodoList
                                        tasksArray = {tasks}
                                        key={el.id}
                                        id={el.id}
                                        filter={el.filter}
                                        title={el.title}
                                        tasks={tasksForTodoList}
                                        changeTaskStatus={changeTaskStatus}
                                        removeTask={removeTask}
                                        filterTasks={filterTasks}
                                        addTask={addTask}
                                        removeTodoList={removeTodoList}
                                        onCnangeListItemHandler={onCnangeListItemHandler}
                                        changeTodolistTitle={changeTodolistTitle}
                                        selectAllItems={selectAllItems}
                                        allSelectItem={el.selectHandler}
                                    />
                                </Paper>
                            </Grid>
                        })
                    }
                </Grid>
            </Container>
        </div>

    );
}

export default AppWithRedux;
